@-webkit-keyframes scale-up-ver-center {
  0% {
    -webkit-transform: scaleY(0.4);
    transform: scaleY(0.4);
  }

  100% {
    -webkit-transform: scaleY(1);
    transform: scaleY(1);
  }
}

@keyframes scale-up-ver-center {
  0% {
    -webkit-transform: scaleY(0.4);
    transform: scaleY(0.4);
  }

  100% {
    -webkit-transform: scaleY(1);
    transform: scaleY(1);
  }
}

@-webkit-keyframes text-focus-in {
  0% {
    -webkit-filter: blur(12px);
    filter: blur(12px);
    opacity: 0;
  }

  100% {
    -webkit-filter: blur(0px);
    filter: blur(0px);
    opacity: 1;
  }
}

@keyframes text-focus-in {
  0% {
    -webkit-filter: blur(12px);
    filter: blur(12px);
    opacity: 0;
  }

  100% {
    -webkit-filter: blur(0px);
    filter: blur(0px);
    opacity: 1;
  }
}

.loading-wrap {
  position: fixed;
  inset: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(255, 255, 255, 0.6);
  backdrop-filter: blur(2px);
  z-index: 9999;
}

.loading-img {
  width: 50px;
  height: auto;
  display: block;
}


.movie-list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 70px;
  padding: 50px 180px;

}

.movie-card {
  display: flex;
  gap: 40px;
  background: #fff;
  border-radius: 4px;
  padding: 20px;
  overflow: visible;
  box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.4);

  .movie-link {
    display: flex;
    gap: 20px;
    color: inherit;
    text-decoration: none;
  }

  .poster {
    width: 120px;
    height: 180px;
    object-fit: cover;
    border-radius: 4px;
    transform: translateY(0px);
    transition: 0.3s;
    box-shadow: 0 6px 10px rgba(0, 0, 0, 0.6);
  }

  .movie-text {
    display: flex;
    flex-direction: column;
    gap: 6px;
    font-size: 14px;

    h3 {
      font-size: 20px;
      margin: 0;
    }

    .year {
      color: #999;
    }

    .summary {
      color: #444;
      line-height: 1.4;
    }

    .genres {
      color: #666;
    }

    .rating {
      font-weight: bold;
    }
  }

  &:hover .poster {
    transform: translateY(-40px);
  }
}

.movie-detail {
  width: 100%;
  max-width: 1100px;
  margin: 100px auto;
  padding: 0 20px;

  display: grid;
  grid-template-columns: 340px 1fr;
  gap: 40px;
  align-items: start;

  .detail-img {
    width: 100%;
    max-width: 320px;
    border-radius: 12px;
    box-shadow: 0 14px 30px rgba(0, 0, 0, 0.25);
    -webkit-animation: scale-up-ver-center 0.6s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
    animation: scale-up-ver-center 0.6s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;

  }

  .detail-text {
    display: flex;
    position: relative;
    flex-direction: column;
    gap: 12px;
    -webkit-animation: text-focus-in 0.4s cubic-bezier(0.550, 0.085, 0.680, 0.530) both;
    animation: text-focus-in 0.4s cubic-bezier(0.550, 0.085, 0.680, 0.530) both;

    h3 {
      font-size: 28px;
      font-weight: 700;
      margin-bottom: 6px;
    }

    .year {
      font-size: 14px;
      color: #999;
    }

    .rating {
      font-size: 16px;
      font-weight: 600;
      color: #ffb400;
    }

    .genres {
      font-size: 14px;
      color: #666;
    }

    .summary {
      margin-top: 16px;
      font-size: 15px;
      line-height: 1.7;
      color: #333;
    }
  }

  .back-btn {
    position: absolute;
    top: 8%;
    right: 22%;

    background: none;
    border: none;
    font-size: 16px;
    padding: 4px 16px;
    background-color: #c1ebb6;
    border-radius: 18px;
    color: #555;
    cursor: pointer;
    transition: 0.3s;
    display: flex;
    align-items: center;
    gap: 6px;

    &:hover {
      color: #000;
      background-color: #abe99c;
    }
  }

}